<template>
	<view class="container">
		<view class="qrImg">
			<text class="tips">请扫码上车</text>
		    <tki-qrcode class="qrcode"
		     v-if="ifShow" 
		     cid="qrcode1" 
		     ref="qrcode" 
		     :val="val" :size="size" 
		     :unit="unit" 
		     :background="background" 
		     :foreground="foreground" 
		     :pdground="pdground" 
		     :icon="icon" :iconSize="iconsize" 
		     :lv="lv" :onval="onval" 
		     :loadMake="loadMake" 
		     :usingComponents="true" />
			
		</view>
	</view>
</template>

<script>
	import tkiQrcode from "@/components/tki-qrcode/tki-qrcode.vue"
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
	    components: {tkiQrcode},
		data(){
			return{
				ifShow: true,
				val: '二维码', // 要生成的二维码值
				size: 250, // 二维码大小
				unit: 'px', // 单位
				background: '#ffffff', // 背景色
				foreground: '#000000', // 前景色
				pdground: '#000000', // 角标色
				icon: '', // 二维码图标
				iconsize: 40, // 二维码图标大小
				lv: 3, // 二维码容错级别 ， 一般不用设置，默认就行
				onval: true, // val值变化时自动重新生成二维码
				loadMake: true, // 组件加载完成后自动生成二维码
				src: '' // 二维码生成后的图片地址或base64
			}
		},
		computed:{
			...mapState(['userId','hasLogin','forcedLogin'])
		},
		onLoad() {
			if(this.hasLogin){
				this.Qrcode()
			}
			else
			{
				uni.showToast({
					title:'请先登录',
					mask:true,
					image:'../../static/img/alert.png',
					icon: 'none',
				})
			}
			
			// if(this.val==" ")
			// {
			// 	this.val='二维码'
			// }
			// console.log(options)
			// if(options.start==" "||options.end==" ")
			// {
			// 	this.val='二维码'
			// }
			// else
			// {
			// 	let data=options.start+options.end
			// 	this.val=data.toString()
			// 	console.log(this.val)
			// }
			
		},
		onShow(){
			if(this.hasLogin){
				this.Qrcode()
			}
			else
			{
				// this.guideToLogin()
			}
		},
		methods:{
			guideToLogin() {
				uni.showModal({
					title: '未登录',
					content: '您未登录，需要登录后才能继续',
					/**
					 * 如果需要强制登录，不显示取消按钮
					 */
					showCancel: this.forcedLogin,
					success: (res) => {
						if (res.confirm) {
							/**
							 * 如果需要强制登录，使用reLaunch方式
							 */
							if (this.forcedLogin) {
								uni.reLaunch({
									url: '../login/login'
								});
							} else {
								uni.navigateTo({
									url: '../login/login'
								});
							}
						}
					}
				});
			},
			Qrcode(){
				var that=this
				uni.request({
					url:'http://mrbus.net:8888/api/user/code/'+this.userId,
					header:{
						"Content-Type": "application/json"
					},
					method:'GET',
					success: (res) => {
						console.log(res.data)
						let ans=res.data;
						if(ans.status_code==200)
						{
							that.val=res.data.message
						}
						else
						{
							
						}
					}
				})
			}
		}
	}
</script>

<style>
	.container{
		width: 100%;
		
	}
	.qrImg{
		width: 100%;
		/* height: 100%; */
		margin-top:20rpx;
		/* border: 1px solid #0000FF; */
		text-align: center;
	}
	.qrcode{
		vertical-align: middle;
		margin: 0 auto;
	}
	.tips{
		width: 100%;
		text-align: center;
		margin-bottom: 10rpx;
		padding-bottom: 20rpx;
		font-size: 22px;
		font-weight: 400;
		font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
	}
</style>
